@import "./design-tokens/scss-variables.scss";
@import "./design-tokens/css-variables.scss";
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  // letter-spacing: -0.25px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


@font-face {
  font-family: "Akagi Pro";
  font-style: normal;
  font-weight: 400;
  src: url("./assets/fonts/AkagiPro-Black.ttf");
}

@font-face {
  font-family: "Akagi Pro Light";
  font-style: normal;
  font-weight: 400;
  src: url("./assets/fonts/AkagiPro-Light.ttf");
}

@font-face {
  font-family: "Mirage-Bold";
  font-style: normal;
  font-weight: 700;
  src: url("./assets/fonts/Mirage-Bold.otf");
}

@font-face {
  font-family: "Mirage-Medium";
  font-style: normal;
  font-weight: 700;
  src: url("./assets/fonts/Mirage-Medium.otf");
}